import React from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import AddUserList from '../../pages/users/AddUsers';
import JobDescription from '../../pages/jobdescription/jobdescription';
import NotFound from '../../pages/notfound/NotFound';
import { lazy } from 'react'

const ProfileMatchList = lazy(() => import('../../pages/profilematching-page/index'))
const UserData = lazy(() => import('../../pages/users/index'))
const Prompt = lazy(() => import('../../pages/prompts/prompt'))

const AppRoutes = () => {
  return (
    <>
      <Routes>

        <Route
          path='/'
          element={
            <Navigate
              to='/users'
              replace
            />
          }
        />
        
        <Route
          path='/users'
          element={<UserData />}
        />
         <Route
          path='/users/add-user'
          element={<AddUserList />}
        />
        <Route
          path='/profile-matching'
          element={<ProfileMatchList />}
        />
        <Route
          path='/job-description'
          element={<JobDescription/>}
        />
        <Route
          path='/prompts'
          element={<Prompt/>}
        />
        <Route
          path='/profile'
          element={<NotFound />}
        />
      </Routes>
    </>
  );
};

export default AppRoutes;
