// EditJobModal.jsx
import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

const EditJobModal = ({ open, onClose, onSave, initialData }) => {
  const [formData, setFormData] = useState({
    jobName: initialData.jobdescriptionName,
    jobDescription: initialData.jobDescription,
  });

  console.log(initialData)
  useEffect(() => {
    setFormData({
      jobName: initialData.jobdescriptionName,
      jobDescription: initialData.jobDescription,
    });
  }, [initialData]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({ ...prevData, [name]: value }));
  };

  const handleSave = () => {
    onSave(initialData.jobdescriptionName, formData.jobName, formData.jobDescription);
    onClose();
  };

  return (
    <Dialog open={open} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle style={{ backgroundColor: '#4880FF', color: 'white', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        Edit Job
        <IconButton onClick={onClose} style={{ color: 'white' }}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent>
        <TextField
          label="Job Name"
          variant="outlined"
          fullWidth
          margin="normal"
          name="jobName"
          value={formData.jobName}
          onChange={handleChange}
        />
        <TextField
          label="Job Description"
          variant="outlined"
          fullWidth
          multiline
          rows={4}
          margin="normal"
          name="jobDescription"
          value={formData.jobDescription}
          onChange={handleChange}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} color="primary" variant="contained" size="small">
          Cancel
        </Button>
        <Button onClick={handleSave} color="primary" variant="contained" size="small">
          Save
        </Button>
      </DialogActions>
    </Dialog>
  );
};

EditJobModal.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
  initialData: PropTypes.object.isRequired,
};

export default EditJobModal;