// JobDescription.jsx
import React, { useEffect, useState } from 'react';
import { DataGrid } from '@mui/x-data-grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Tooltip from '@mui/material/Tooltip';
import Pagination from '@mui/material/Pagination';
import MenuItem from '@mui/material/MenuItem';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import AddJobModal from './AddJobModal';
import EditJobModal from './EditJobModal';
import DeleteJobModal from './DeleteJobModal';

const JobDescription = () => {
  const [isSmallScreen, setIsSmallScreen] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [entriesToShow, setEntriesToShow] = useState(10); // Initial value is set to 10
  const [isAddModalOpen, setAddModalOpen] = useState(false);
  const [isEditModalOpen, setEditModalOpen] = useState(false);
  const [selectedJobData, setSelectedJobData] = useState(null);
  const [selectedJobToDelete, setSelectedJobToDelete] = useState(null);
  const [isDeleteModalOpen, setDeleteModalOpen] = useState(false);

  const [rowsData, setRowsData] = useState([
    { jobdescriptionName: 'Job 1', jobDescription: 'Description 1' },
    { jobdescriptionName: 'Job 2', jobDescription: 'Description 2' },
    { jobdescriptionName: 'Job 3', jobDescription: 'Description 3' },
    { jobdescriptionName: 'Job 4', jobDescription: 'Description 4' },

    // Add more rows as needed
  ]);

  useEffect(() => {
    const handleResize = () => {
      setIsSmallScreen(window.innerWidth <= 600);
    };

    handleResize();

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const columns = [
    { field: 'jobdescriptionName', headerName: 'Job Description Name', flex: 1, headerClassName: 'bg-primary text-white' },
    { field: 'jobDescription', headerName: 'Job Description', flex: 1, headerClassName: 'bg-primary text-white' },
    {
      field: 'actions',
      headerName: 'Actions',
      flex: 1,
      renderCell: (params) => (
        <>
          <Tooltip title="Edit" arrow>
            <Button
              size="small"
              style={{ marginRight: '8px' }}
              onClick={() => handleEditClick(params.row)}
            >
              <EditIcon color="primary" />
            </Button>
          </Tooltip>
          {isSmallScreen ? (
            <Tooltip title="Delete" arrow>
              <Button
                size="small"
                onClick={() => handleDeleteClick(params.row.jobdescriptionName)}
              >
                <DeleteIcon color="primary" />
              </Button>
            </Tooltip>
          ) : (
            <Button
              size="small"
              onClick={() => handleDeleteClick(params.row.jobdescriptionName)}
            >
              <DeleteIcon color="primary" />
            </Button>
          )}
        </>
      ),
      headerClassName: 'bg-primary text-white',
    },
  ];

  const getRowId = (row) => row.jobdescriptionName;

  const filteredRows = rowsData.filter(
    (row) =>
      (row.jobdescriptionName &&
        row.jobdescriptionName.toLowerCase().includes(searchTerm.toLowerCase())) ||
      (row.jobDescription &&
        row.jobDescription.toLowerCase().includes(searchTerm.toLowerCase()))
  );

  const CustomPagination = () => (
    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '8px' }}>
      <div>
        {/* Removed Showing 1 to {Math.min(currentPage * (isSmallScreen ? 3 : 5), filteredRows.length)} of {filteredRows.length} entries */}
      </div>
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <Typography style={{ marginRight: '8px' }}>Rows</Typography>
        <TextField
          select
          variant="outlined"
          size="small"
          value={entriesToShow}
          onChange={(e) => setEntriesToShow(e.target.value)}
          // style={{ width: '60px' }} // Reduced the width
        >
          {[5, 10, 20].map((option) => (
            <MenuItem key={option} value={option}>
              {option}
            </MenuItem>
          ))}
        </TextField>
        {/* <Typography style={{ margin: '0 8px' }}>Entries</Typography> */}
        <Pagination
          color="primary"
          count={Math.ceil(filteredRows.length / entriesToShow)}
          page={currentPage}
          onChange={(event, value) => setCurrentPage(value)}
        />
      </div>
    </div>
  );

  const handleAddModalOpen = () => {
    setAddModalOpen(true);
  };

  const handleAddModalClose = () => {
    setAddModalOpen(false);
  };

  const handleEditClick = (rowData) => {
    setSelectedJobData(rowData);
    setEditModalOpen(true);
  };

  const handleEditModalClose = () => {
    setEditModalOpen(false);
    setSelectedJobData(null);
  };

  const handleSaveNewJob = (newJobName, newJobDescription) => {
    const newJob = { jobdescriptionName: newJobName, jobDescription: newJobDescription };
    setRowsData((prevRows) => [...prevRows, newJob]);
    handleAddModalClose();
  };

  const handleSaveEditJob = (originalJobName, newJobName, newJobDescription) => {
    setRowsData((prevRows) =>
      prevRows.map((row) =>
        row.jobdescriptionName === originalJobName
          ? { jobdescriptionName: newJobName, jobDescription: newJobDescription }
          : row
      )
    );
    handleEditModalClose();
  };

  const handleDeleteClick = (jobName) => {
    setSelectedJobToDelete(jobName);
    setDeleteModalOpen(true);
  };

  const handleDeleteModalClose = () => {
    setSelectedJobToDelete(null);
    setDeleteModalOpen(false);
  };

  const handleDeleteJob = (jobName) => {
    setRowsData((prevRows) => prevRows.filter((row) => row.jobdescriptionName !== jobName));
    handleDeleteModalClose();
  };

  return (
    <div>
      <Breadcrumbs aria-label="breadcrumb">
  <Typography color="textPrimary">Job Descriptions</Typography>
</Breadcrumbs>


      <Card variant="outlined" style={{ marginTop: '16px' }}>
        <CardContent>
          <Grid container spacing={2} alignItems="center" justifyContent="flex-end">
            <Grid item>
              <Tooltip title="Search Job Descriptions" arrow>
                <TextField
                  label="Search"
                  variant="outlined"
                  size="small"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  style={{ marginRight: '8px' }}
                />
              </Tooltip>
            </Grid>
            <Grid item>
              <Tooltip title="Add Job Description" arrow>
                <Button
                  variant="contained"
                  color="primary"
                  size="small"
                  style={{ borderRadius: '10px', marginLeft: '8px' }}
                  onClick={handleAddModalOpen}
                >
                  Add +
                </Button>
              </Tooltip>
            </Grid>
            <Grid item xs={12}>
              <div style={{ width: '100%' }}>
                <DataGrid
                  rows={filteredRows}
                  columns={columns}
                  pageSize={entriesToShow}
                  autoHeight
                  getRowId={getRowId}
                  disableRowSelectionOnClick
                  disableColumnMenu
                  pagination
                  paginationMode="server"
                  onPageChange={(params) => setCurrentPage(params.page)}
                  components={{
                    Pagination: CustomPagination,
                  }}
                />
              </div>
            </Grid>
          </Grid>
        </CardContent>
      </Card>

      <AddJobModal open={isAddModalOpen} onClose={handleAddModalClose} onSave={handleSaveNewJob} />
      {selectedJobData && (
        <EditJobModal
          open={isEditModalOpen}
          onClose={handleEditModalClose}
          onSave={handleSaveEditJob}
          initialData={selectedJobData}
        />
      )}
      {selectedJobToDelete && (
        <DeleteJobModal
          open={isDeleteModalOpen}
          onClose={handleDeleteModalClose}
          onDelete={handleDeleteJob}
          jobName={selectedJobToDelete}
        />
      )}
    </div>
  );
};

export default JobDescription;
