import { useState} from 'react';


import FormControlLabel from "@mui/material/FormControlLabel";
import { IconButton, InputAdornment, TextField } from '@mui/material';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Button from "@mui/material/Button";
import Checkbox from "@mui/material/Checkbox";
// import Link from "@mui/material/Link";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { Visibility, VisibilityOff } from "@mui/icons-material";
import BusinessCenterIcon from '@mui/icons-material/BusinessCenter';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import { useNavigate } from 'react-router-dom'
import SnackbarComponent from '../components/toastmessage'
import toast from "react-hot-toast"
// import { getHomeRouteForLoggedInUser } from '../utility/Utils'
import { useForm } from 'react-hook-form'
import axios from 'axios'

import GlobalVariable from '../path/global'


const baseApiUrl = GlobalVariable.BASE_API_URL
const LoginForm = () => {
  const navigate = useNavigate();
 
  //Login formprops
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [isSubmitDisabled, setIsSubmitDisabled] = useState(false)
  const [showPassword, setShowPassword] = useState(false)

  //useForms Error handling
  const {
    register,
    handleSubmit,
    formState: { errors }
  } = useForm({})


  

  const onSubmit = (data) => {
   
    setIsSubmitDisabled(true)
   
    axios.post(`${baseApiUrl}job_analysis/user/login`, {
      email: data.email,
      password: data.password
    })
    .then(response => {
        console.log(response, "-------------------------");
        if (response.data.status_code === 200) {
            const { user, token_type, access_token } = response.data;
            // Now you can use user, token_type, and access_token as needed

            const userData = {
              user, 
              token_type, 
              access_token
            }
            // Store updated credentials in local storage
            localStorage.setItem('userdetails', JSON.stringify(userData));
    
            // Force component update or navigation
            navigateAfterRender();
    
            return <SnackbarComponent message="This is a success Alert at top right" position={{ vertical: 'top', horizontal: 'right', color: 'success' }}/>
            console.log("successful");
        }
    })
    .catch(error => {
        console.error("Error:", error);
        if (error.response && error.response.data.detail.status === 401 ) {
            return <SnackbarComponent message="This is a success Alert at top right" position={{ vertical: 'top', horizontal: 'right', color: 'danger'}}/>
        } else if (error.response && error.response.data.status_code === 422) {
            // Handle other specific status codes as needed
        } else {
            // Handle generic error
        }
    })
    .finally(() => {
        setIsSubmitDisabled(false); // Enable the submit button after login attempt
    });
  
  }

  const navigateAfterRender = () => {
    // After the component re-renders, check local storage and navigate
    const localData = JSON.parse(localStorage.getItem('userdetails'));
    if ( localData.user.roleId === '1') {
      window.location.reload('/users');
    } else {
      navigate('/profile-matching');
      window.location.reload()

    }
  };
  const handleEmailChange = (e) => {
    const { value } = e.target
    setEmail(value)
  }

  const handlePasswordChange = (e) => {
    const { value } = e.target
    setPassword(value)
  }
  const handleClickShowPassword = () => {
    setShowPassword(!showPassword)
  }
  return (
    
    <div className='loginForm-container'>
      <Container component="main" maxWidth="sm" justifyContent="flex-end">
        <Card sx={{ minWidth: 275, marginTop: 8, borderRadius: 5 }}>
          <CardContent>
            <Box
              sx={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
              }}
            >
              <Box display={'flex'} width={'100%'} className="p-5px">
                <BusinessCenterIcon color='primary' sx={{ fontSize: 30 }} style={{ marginRight: '6px' }} />
                <Typography color='primary' paddingRight={1} sx={{ fontSize: 25 }} style={{ fontWeight: '700' }} >
                  Job
                </Typography>
                <Typography sx={{ fontSize: 25 }} style={{ fontWeight: '700' }}>
                  Analysis
                </Typography>
              </Box>
              <Box className="login-form" width={'100%'}>
                <Grid container justifyContent={'center'} marginTop={2}>

                  <Typography mt={2} sx={{ fontSize: 32 }} style={{ fontWeight: '700' }} >
                    Login to Account
                  </Typography>
                  <Typography mt={2} sx={{ fontSize: 18 }} >
                    Please enter your email and password to continue
                  </Typography>

                </Grid>
                <Box component="form" onSubmit={handleSubmit(onSubmit)} noValidate sx={{ mt: 1 }} paddingBottom={5}>
                  <TextField
                    margin="normal"
                    required
                    fullWidth
                    sm={12}
                    id="email"
                    label="Email Address"
                    name="email"
                    {...register('email', {
                      required: 'Email is required'
                    })}
                    error={Boolean(errors.email)}
                    helperText={errors.email?.message}
                    value={email}
                    onChange={(e) => {
                      handleEmailChange(e)
                    }}
                    autoComplete="email"
                    autoFocus
                  />
                  <TextField
                    margin="normal"
                    required
                    fullWidth
                    type={showPassword ? "text" : "password"}
                    name="password"
                    value={password}
                    label="Password"
                    {...register('password', {
                      required: 'Password is required',
                      minLength: {
                        value: 6,
                        message: 'Password must be at least 6 characters',
                      },
                    })}
                    error={Boolean(errors.password)}
                    helperText={errors.password?.message}
                    onChange={(e) => {
                      handlePasswordChange(e)
                    }}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton
                            aria-label="toggle password visibility"
                            onClick={handleClickShowPassword}
                            edge="end"
                          >
                            {showPassword ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                    id="password"
                    autoComplete="current-password"
                  />
                  <Grid container alignItems={'center'} justifyContent={'space-between'} display={'inline-flex'}>
                    <Grid item xs={12}>
                      {/* <FormControlLabel
                        control={<Checkbox  {...register('rememberMe')} value="remember" color="primary" />}
                        label="Remember me"
                      /> */}
                    </Grid>
                    {/* <Grid item xs={6} display={'flex'} justifyContent={'end'}>
                  <Link href="#" variant="body2">
                      Forgot password?
                    </Link>
                </Grid> */}
                  </Grid>
                  <Box display={'flex'} justifyContent={'center'}>
                    <Button
                      type="submit"
                      fullWidth
                      variant="contained"
                      sx={{ mt: 3, mb: 2, width: 400, borderRadius: 2 }}
                      disabled={isSubmitDisabled}
                      className='btn btn-submit'
                      size="large"
                    >
                      Sign In
                    </Button>
                  </Box>


                </Box>
              </Box>

            </Box>
          </CardContent>

        </Card>

      </Container>
    </div>

  );
};

export default LoginForm;
