// AddJobModal.jsx
import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import { styled } from '@mui/system';

const StyledButton = styled(Button)(({ theme }) => ({
  fontSize: '18px',
  '&:hover': {
    color: theme.palette.common.white,
    backgroundColor: theme.palette.primary.main,
  },
}));

const AddJobModal = ({ open, onClose, onSave }) => {
  const [formData, setFormData] = useState({
    jobName: '',
    jobDescription: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({ ...prevData, [name]: value }));
  };

  const handleSave = () => {
    onSave(formData.jobName, formData.jobDescription);

    const existingData = JSON.parse(localStorage.getItem('jobData')) || [];
    const newData = { jobName: formData.jobName, jobDescription: formData.jobDescription };
    const updatedData = [...existingData, newData];
    localStorage.setItem('jobData', JSON.stringify(updatedData));

    setFormData({ jobName: '', jobDescription: '' });
    onClose();
  };

  return (
    <Dialog open={open} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle sx={{ backgroundColor: '#4880FF', color: 'white', display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '8px', fontSize: '20px' }}>
        <span>Add Job</span>
        <IconButton onClick={onClose} style={{ color: 'white' }}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent>
        <TextField
          label="Job Name"
          variant="outlined"
          fullWidth
          margin="normal"
          name="jobName"
          value={formData.jobName}
          onChange={handleChange}
        />
        <TextField
          label="Job Description"
          variant="outlined"
          fullWidth
          multiline
          rows={4}
          margin="normal"
          name="jobDescription"
          value={formData.jobDescription}
          onChange={handleChange}
        />
      </DialogContent>
      <DialogActions>
        <StyledButton
          onClick={onClose}
          color="primary"
          variant="outlined"
          size="small"
        >
          Cancel
        </StyledButton>
        <Button
          onClick={handleSave}
          color="primary"
          variant="contained"
          size="small"
          style={{ fontSize: '18px' }}
        >
          Save
        </Button>
      </DialogActions>
    </Dialog>
  );
};

AddJobModal.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
};

export default AddJobModal;
