import './App.css';
import { Routes, Route, Navigate } from 'react-router-dom';
import MainLayout from './Layout/MainLayout';
import LoginRoute from './routes/authRoutes/LoginRoute';
// 
import NotFound from './pages/notfound/NotFound';
import { createTheme, ThemeProvider } from '@mui/material/styles';


const theme = createTheme({
  typography: {
    fontFamily: [
      'Nunito Sans',
      'sans-serif'
    ].join(','),
  },
  components: {
    MuiButton: {
      styleOverrides: {
        // Name of the slot
        root: {
          // Some CSS
          fontFamily: 'Nunito Sans, sans-serif', 
          fontWeight: 700,
          textTransform: 'capitalize',
          letterSpacing: '1px',
          color: "#ffffff !important",
          fontSize: '20px'
        },
      },
    },
  }
  
});

function App() {

  const getUserDetails = JSON.parse(localStorage.getItem('userdetails'));
  const isAuthenticated = getUserDetails && getUserDetails.access_token;
  return (
    <ThemeProvider theme={theme}>
      <div className='app'>
      <Routes>
      <Route
            path='/login'
            element={isAuthenticated ? <Navigate to='/profile-matching' replace /> : <LoginRoute />}
          />
          <Route
            path='/*'
            element={isAuthenticated ? <MainLayout /> : <Navigate to='/login' replace />}
          />
           <Route path="/profile" element={<NotFound />} />
         
          <Route path='/not-found' element={<NotFound />} />
          <Route path='*' element={<Navigate to='/not-found' />} />
        </Routes>
    </div>
    </ThemeProvider>
    
  );
}

export default App;
