import React, { useState, useEffect, useCallback } from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/DeleteOutlined';
import SaveIcon from '@mui/icons-material/Save';
import CancelIcon from '@mui/icons-material/Close';
import {
  DataGrid,
  GridRowModes,
  GridActionsCellItem,
  GridRowEditStopReasons,
  GridToolbarContainer,
} from '@mui/x-data-grid';

const generateRandomId = () => Math.random().toString(36).substring(7);

function EditToolbar(props) {
  const { setRows, setRowModesModel } = props;

  const handleClick = () => {
    const id = generateRandomId();
    setRows((oldRows) => [...oldRows, { id, promptName: '', promptDescription: '', isNew: true }]);
    setRowModesModel((oldModel) => ({
      ...oldModel,
      [id]: { mode: GridRowModes.Edit, fieldToFocus: 'promptName' },
    }));
  };

  return (
    <GridToolbarContainer sx={{ marginBottom: '10px', marginTop: '10px' }}>
      <Box sx={{ display: 'flex', justifyContent: 'flex-end', flexGrow: 1 }}>
        <Button color="primary" className="bg-primary" startIcon={<AddIcon />} onClick={handleClick}>
          Add Prompt
        </Button>
      </Box>
    </GridToolbarContainer>
  );
}

export default function PromptDataGrid({ rowsData, onRowsChange,updatePromptRows }) {
  const [rows, setRows] = useState([]);
  const [rowModesModel, setRowModesModel] = useState({});
console.log(rows,"rows")
  const handleRowEditStop = useCallback((params, event) => {
    if (params.reason === GridRowEditStopReasons.rowFocusOut) {
      event.defaultMuiPrevented = true;
    }
  }, []);

  const handleEditClick = useCallback(
    (id) => () => {
      setRowModesModel({ ...rowModesModel, [id]: { mode: GridRowModes.Edit } });
    },
    [rowModesModel]
  );

  const handleSaveClick = useCallback(
    (id) => () => {
      setRowModesModel({ ...rowModesModel, [id]: { mode: GridRowModes.View } });
    },
    [rowModesModel]
  );

  const handleDeleteClick = useCallback(
    (id) => () => {
      setRows((prevRows) => prevRows.filter((row) => row.id !== id));
    },
    []
  );

  const handleCancelClick = useCallback(
    (id) => () => {
      setRowModesModel({
        ...rowModesModel,
        [id]: { mode: GridRowModes.View, ignoreModifications: true },
      });

      const editedRow = rows.find((row) => row.id === id);
      if (editedRow && editedRow.isNew) {
        setRows((prevRows) => prevRows.filter((row) => row.id !== id));
      }
    },
    [rows, rowModesModel]
  );

  const processRowUpdate = useCallback(
    (newRow) => {
      const updatedRow = { ...newRow, isNew: false };
      setRows((prevRows) => prevRows.map((row) => (row.id === newRow.id ? updatedRow : row)));

      return updatedRow;
    },
    []
  );

  const handleRowModesModelChange = useCallback((newRowModesModel) => {
    setRowModesModel(newRowModesModel);
    console.log("567457")
    updatePromptRows(rows)
  }, []);



  useEffect(() => {
    if (rowsData && rowsData.length > 0) {
      setRows(rowsData);
    }
  }, [rowsData]);



  const columns = [
    { field: 'promptName', headerName: 'Prompt Name', flex: 1, editable: true, headerClassName: 'bg-primary text-white text-size' },
    { field: 'promptDescription', headerName: 'Prompt Description', flex: 1, editable: true, headerClassName: 'bg-primary text-white text-size' },
    {
      field: 'actions',
      type: 'actions',
      headerName: 'Actions',
      width: 150,
      flex: 1,
      cellClassName: 'actions',
      headerClassName: 'bg-primary text-white text-size',
      getActions: ({ id }) => {
        const isInEditMode = rowModesModel[id]?.mode === GridRowModes.Edit;

        if (isInEditMode) {
          return [
            <GridActionsCellItem
              icon={<SaveIcon />}
              label="Save"
              className="button-a"
              sx={{
                color: 'primary.main',
              }}
              onClick={handleSaveClick(id)}
            />,
            <GridActionsCellItem
              icon={<CancelIcon />}
              label="Cancel"
              className="textPrimary button-a"
              onClick={handleCancelClick(id)}
              color="inherit"
            />,
          ];
        }

        return [
          <GridActionsCellItem
            icon={<EditIcon />}
            label="Edit"
            className="textPrimary button-a"
            onClick={handleEditClick(id)}
            color="inherit"
          />,
          <GridActionsCellItem
            icon={<DeleteIcon />}
            label="Delete"
            onClick={handleDeleteClick(id)}
            color="inherit"
            className="button-a"
          />,
        ];
      },
    },
  ];

  return (
    <Box
      sx={{
        height: 500,
        width: '100%',
        '& .actions': {
          color: 'text.secondary',
        },
        '& .textPrimary': {
          color: 'text.primary',
        },
      }}
    >
      <DataGrid
        rows={rows}
        columns={columns}
        editMode="row"
        rowModesModel={rowModesModel}
        onRowModesModelChange={handleRowModesModelChange}
        onRowEditStop={handleRowEditStop}
        processRowUpdate={processRowUpdate}
      
        slots={{
          toolbar: EditToolbar,
        }}
        slotProps={{
          toolbar: { setRows, setRowModesModel },
        }}
      />
    </Box>
  );
}