// ** User List Component
import { Fragment, useState, useEffect  } from 'react'
import { IconButton, InputAdornment, TextField } from '@mui/material';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import { CardContent, Select, MenuItem, InputLabel, FormControl } from '@mui/material';
import Typography from '@mui/material/Typography';
import ResBreadcrumbs from '../../components/Breadcrumbs'
import { Visibility, VisibilityOff } from "@mui/icons-material";
import { useForm } from 'react-hook-form'
import GlobalVariable from '../../path/global'
import axios from 'axios';
import PromptDataGrid from './PromptDataGrid/PromptDataGrid';
import { Button } from '@mui/material';
import ToastSnackbars from '../../components/toastmessage'
import toast from "react-hot-toast"

const baseApiUrl = GlobalVariable.BASE_API_URL


const initialRows = [
    { id: '1', promptName: 'Custom Prompt 1', promptDescription: 'Custom Description 1' },
    { id: '2', promptName: 'Custom Prompt 2', promptDescription: 'Custom Description 2' },
  ];
const AddUserList = () => {
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [isSubmitDisabled, setIsSubmitDisabled] = useState(false)
    const [showPassword, setShowPassword] = useState(false)
    const [password, setPassword] = useState('')
    const [selectedRole, setSelectedRole] = useState('');
    const [promptRows, setPromptRows] = useState([]);
console.log(promptRows,"pr")
    


    const {
        register,
        handleSubmit,
        formState: { errors }
      } = useForm({})

      useEffect(() => {
        // Fetch data from your API endpoint
        const fetchData = async () => {
          try {
            const response = await axios.get(`${baseApiUrl}job_analysis/roles`);
            console.log(response)
            // setRoles(response.data); // Assuming the result is an array of objects with id and name fields
          } catch (error) {
            console.error('Error fetching data:', error);
          }
        };
    
        fetchData();
      }, []);

      useEffect(() => {
        setPromptRows(initialRows)
      }, [promptRows]);

    const handleNameChange = (e) => {
        const { value } = e.target
        setName(value)
      }

      const handleEmailChange = (e) => {
        const { value } = e.target
        setEmail(value)
      }

      const handlePasswordChange = (e) => {
        const { value } = e.target
        setPassword(value)
      }
      const handleClickShowPassword = () => {
        setShowPassword(!showPassword)
      }

      const handleChange = (event) => {
        setSelectedRole(event.target.value);
      };

      const dummyData = [
        { id: 1, name: 'Admin' },
        { id: 2, name: 'Account Manager' },
        { id: 3, name: 'Recruiter' },
    ];

    const updatePromptRows = (newRows) => {
        console.log(newRows,"nree")
        setPromptRows(newRows);
        console.log(promptRows,"rttttttttttttttttttttt")
      };
   
      const onRowsChange = (newRows) => {
        // Handle the change of rows here if needed
        console.log('Rows changed:', newRows);
      };
      const onSubmit  = async () => {
        setIsSubmitDisabled(true)
        const editedPrompts = promptRows.map((row) => ({
            id: row.id,
            promptName: row.promptName,
            promptDescription: row.promptDescription,
          }));
        
          const requestData = {
            user: { name, email, password, roleId: selectedRole },
            prompts: editedPrompts,
          };
        
          console.log(requestData, "------------- Users Data Validate");
        await axios.post(`${baseApiUrl}job-analysis/addusers`,{ requestData})
        .then(response => {
            if (response.data.status_code === 200) {
                toast(t => (<ToastSnackbars t={t} message={"You have successfully logged"} status={"success"} />));
            console.log("successful");
            }
        }).catch(error =>{
            if (error.response && error.response.data.status_code === 401) {
                toast(t => (<ToastSnackbars t={t} message={"Invalid account, Email or Password do not match."} status={"danger"} />));
            } else if (error.response && error.response.data.status_code === 422) {
                // Handle other specific status codes as needed
            } else {
                // Handle generic error
            }
        }).finally(() => {
            setIsSubmitDisabled(false); // Enable the submit button after login attempt
        });

      
      }
    return (
        <Fragment>
            <ResBreadcrumbs title='Users ' data={[{ title: 'Dashboard' }]}/>

            <div className='app-user-list'>
                <Card>
                        <Box sx={{padding: '10px 15px'}}>
                                <Typography fontSize={20} fontWeight={700}>Add User</Typography>
                        </Box>
                        <CardContent>
                            <Box component={'form'} onSubmit={handleSubmit(onSubmit)}>
                            <Grid container spacing={2} alignItems={'center'} sx={{padding: '0px 0px 16px '}}>
                                <Grid item xs={12} sm={3} md={3}>
                                    <TextField
                                        margin="normal"
                                        required
                                        fullWidth
                                        sm={12}
                                        id="name"
                                        label="Name"
                                        name="name"
                                        {...register('name', {
                                        required: 'Name is required'
                                        })}
                                        error={Boolean(errors.name)}
                                        helperText={errors.name?.message}
                                        value={name}
                                        onChange={(e) => {
                                        handleNameChange(e)
                                        }}
                                        autoComplete="Off"
                                        autoFocus
                                    />
                                </Grid>
                                <Grid item xs={12} sm={3} md={3}>
                                <TextField
                                        margin="normal"
                                        required
                                        fullWidth
                                        sm={12}
                                        id="email"
                                        label="Email"
                                        name="email"
                                        {...register('email', {
                                        required: 'Email is required'
                                        })}
                                        error={Boolean(errors.email)}
                                        helperText={errors.email?.message}
                                        value={email}
                                        onChange={(e) => {
                                        handleEmailChange(e)
                                        }}
                                        autoComplete="Off"
                                        
                                    />
                                </Grid>
                                <Grid item xs={12} sm={3} md={3}>
                                <TextField
                                    margin="normal"
                                    required
                                    fullWidth
                                    type={showPassword ? "text" : "password"}
                                    name="password"
                                    value={password}
                                    label="Password"
                                    {...register('password', {
                                    required: 'Password is required',
                                    minLength: {
                                        value: 6,
                                        message: 'Password must be at least 6 characters',
                                    },
                                    })}
                                    error={Boolean(errors.password)}
                                    helperText={errors.password?.message}
                                    onChange={(e) => {
                                    handlePasswordChange(e)
                                    }}
                                    InputProps={{
                                    endAdornment: (
                                        <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword}
                                            edge="end"
                                        >
                                            {showPassword ? <VisibilityOff /> : <Visibility />}
                                        </IconButton>
                                        </InputAdornment>
                                    ),
                                    }}
                                    id="password"
                                    autoComplete="current-password"
                                />
                                </Grid>
                                <Grid item xs={12} sm={3} md={3}>
                                <FormControl fullWidth sx={{top: '3px'}}>
                                    <InputLabel id="select-label">Select a Role</InputLabel>
                                    <Select
                                        labelId="select-label"
                                        id="select"
                                        value={selectedRole}
                                        onChange={handleChange}
                                        label="Select an Option"
                                    >
                                        {dummyData.map((item) => (
                                        <MenuItem key={item.id} value={item.id}>
                                            {item.name}
                                        </MenuItem>
                                        ))}
                                    </Select>
                                    </FormControl>
                                </Grid>
                            
                            </Grid>
                      
                          
                            <Grid container spacing={2}>
                                <Grid item xs={12} sm={12}>
                                    <PromptDataGrid rowsData={promptRows}  onRowsChange={onRowsChange}  updatePromptRows={updatePromptRows}/>
                                </Grid>
                            </Grid>
                            <Grid container justifyContent={'flex-end'}>
                            <Button color="primary" type="submit" disabled={isSubmitDisabled} className='bg-primary' sx={{marginTop: '10px'}}>
                                Save User
                                            </Button>
                            </Grid>
                            
                            </Box>
                        </CardContent>
                </Card>
               
            </div>
        </Fragment>
    )
}

export default AddUserList
